  let m5 = m4.clone();
  rotateAxis(m5, m5, pi/2, [0, 0, 1]);

  let m6 = m4.clone();
  rotateAxis(m6, m6, pi, [0, 0, 1]);

  let m7 = m4.clone();
  rotateAxis(m7, m7, 3*pi/2, [0, 0, 1]);

  const originalMatrix = [id, m1, m2, m3, m4, m5, m6, m7];
  const matrixArray = [];
  const newVertices = [
    [-1, 1],
    [1, 1],
    [1, -1],
    [-1, -1]
  ];
  tile(arr => {
    matrixArray.push.apply(matrixArray, originalMatrix.map(m => {
      m = m.clone();
      m.translate(arr);
      return m;
    }));
  }, iterations, {vertices: newVertices});
