import * as euclideanTexture from './euclidean/texture.js';
import * as hyperbolicTexture from './hyperbolic/texture.js';
import * as sphericalTexture from './spherical/texture.js';

import {Texture2D, loadTextures} from 'luma.gl';
import {
  polygonMesh,
  circleMesh,
  sectionSolidMesh,
  sphericalSectionIter,
  sphericalSectionPoly,
  sphericalSectionSolid,
  euclideanSectionPoly,
  euclideanSectionSolid,
  quad
} from './models.js';

export function createSymmetries(gl, state) {
  const {symmetries} = state;
  return new Promise((resolve, reject) => {
    Promise.all(
      symmetries.map(s => createSymmetry(gl, s))
    ).then((res) => {
      resolve({symmetries: res});
    });
  });
}

function createSymmetry(gl, descriptor) {
  const {motif, iterations, type, orbifold} = descriptor;
  const {color, depth, mesh} = motif;
  return new Promise((resolve, reject) => {
    Promise.all(
      [depth, color].map((config) => {
        return textureFactory[type][config.type](gl, config, descriptor);
      })
    ).then((textures) => {
      meshFactory[mesh.type](gl, textures, mesh, descriptor)
        .then(({model, transform}) => {
          resolve({
            model,
            transform,
            textures,
            descriptor
          });
        });
    });
  });
}

const textureFactory = {
  euclidean: euclideanTexture.default,
  hyperbolic: hyperbolicTexture.default,
  spherical: sphericalTexture.default
};

const meshFactory = {
  polygon: polygonMesh,
  circle: circleMesh,
  sectionSolid: sectionSolidMesh,
  quad,
  sphericalIter: sphericalSectionIter,
  sphericalPoly: sphericalSectionPoly,
  sphericalSolid: sphericalSectionSolid,
  euclideanPoly: euclideanSectionPoly,
  euclideanSolid: euclideanSectionSolid
};





