import {Vector4, Matrix4} from 'math.gl';
import * as vec3 from '../vec3.js';
import {rotate as rotateAxis} from 'gl-mat4';

const cos = Math.cos;
const sin = Math.sin;
const pi = Math.PI;
const pi2 = pi * 2;
const sqrt = Math.sqrt;
const atan2 = Math.atan2;
const distSq = (p) => p.x * p.x + p.y * p.y;
const asin = Math.asin;
const acos = Math.acos;

export function getSphericalParams(layoutType, N) {
  let p1 = 0;
  let p2 = 0;
  let p3 = 0;
  let p4;
  let angle1, angle2, angle3, angle4;
  let m, a, b, c;

  switch (layoutType) {
    case '*NN':
    case 'NN':
      p1 = [0, 1, 0];
      p2 = [0, 0, 1];
      p3 = [sin(pi2/N), 0, cos(pi2/N)];
      p4 = [0, -1, 0];
    break;
    case '*22N':
    case '22N':
    case 'N*':
    case '2*N':
    case 'Nx':
      p1 = [0, 1, 0];
      p2 = [0, 0, 1];
      p3 = [sin(pi2/N), 0, cos(pi2/N)];
    break;

    case '*432':
      p1 = [0, 1, 0];
      [a, b, c] = sphericalTriangle(pi/4, pi/3, pi/2);
      angle1 = b;
      p2 = [0, cos(angle1), sin(angle1)];
      angle2 = c;
      p3 = new Vector4([0, cos(angle2), sin(angle2), 1]);
      m = new Matrix4().identity();
      rotateAxis(m, m, pi/4, [0, 1, 0]);
      m.transformVector(p3, p3);
      p3 = [p3[0], p3[1], p3[2]];
    break;

    case '432':
      p1 = [0, 1, 0];
      [a, b, c] = sphericalTriangle(pi/4, pi/3, pi/2);
      angle1 = c;
      p2 = [0, cos(angle1), sin(angle1)];
      angle2 = c;
      p3 = new Vector4([0, cos(angle2), sin(angle2), 1]);
      m = new Matrix4().identity();
      rotateAxis(m, m, 2*pi/4, [0, 1, 0]);
      m.transformVector(p3, p3);
      p3 = [p3[0], p3[1], p3[2]];
    break;

    case '*532':
      p1 = [0, 1, 0];
      [a, b, c] = sphericalTriangle(pi/5, pi/3, pi/2);
      angle1 = b;
      p2 = [0, cos(angle1), sin(angle1)];
      angle2 = c;
      p3 = new Vector4([0, cos(angle2), sin(angle2), 1]);
      m = new Matrix4().identity();
      rotateAxis(m, m, pi/5, [0, 1, 0]);
      m.transformVector(p3, p3);
      p3 = [p3[0], p3[1], p3[2]];
    break;

    case '532':
      p1 = [0, 1, 0];
      [a, b, c] = sphericalTriangle(pi/5, pi/3, pi/2);
      angle1 = c;
      p2 = [0, cos(angle1), sin(angle1)];
      angle2 = c;
      p3 = new Vector4([0, cos(angle2), sin(angle2), 1]);
      m = new Matrix4().identity();
      rotateAxis(m, m, 2*pi/5, [0, 1, 0]);
      m.transformVector(p3, p3);
      p3 = [p3[0], p3[1], p3[2]];
    break;


    case '*332':
      p1 = [0, 1, 0];
      [a, b, c] = sphericalTriangle(pi/3, pi/3, pi/2);
      angle1 = b;
      p2 = [0, cos(angle1), sin(angle1)];
      angle2 = c;
      p3 = new Vector4([0, cos(angle2), sin(angle2), 1]);
      m = new Matrix4().identity();
      rotateAxis(m, m, pi/3, [0, 1, 0]);
      m.transformVector(p3, p3);
      p3 = [p3[0], p3[1], p3[2]];
    break;

    case '332':
      p1 = [0, 1, 0];
      [a, b, c] = sphericalTriangle(pi/3, pi/3, pi/2);
      angle1 = c;
      p2 = [0, cos(angle1), sin(angle1)];
      angle2 = c;
      p3 = new Vector4([0, cos(angle2), sin(angle2), 1]);
      m = new Matrix4().identity();
      rotateAxis(m, m, 2*pi/3, [0, 1, 0]);
      m.transformVector(p3, p3);
      p3 = [p3[0], p3[1], p3[2]];
    break;

    case '3*2':
      p1 = [0, 1, 0];
      [a, b, c] = sphericalTriangle(pi/4, pi/3, pi/2);
      angle4 = c;
      p4 = [0, cos(angle4), sin(angle4)];
      m = new Matrix4().identity();
      rotateAxis(m, m, pi/4, [0, 1, 0]);
      m.transformVector(p4, p4);
      // cosine rule to get length of c:
      let aux = acos(cos(pi/4) * cos(pi/4) + sin(pi/4) * sin(pi/4) * cos(pi/2));
      p1 = [0, 1, 0];
      [a, b, c] = [aux, pi/4, pi/4];
      angle1 = c;
      p2 = [0, cos(angle1), sin(angle1)];
      angle2 = c;
      p3 = new Vector4([0, cos(angle2), sin(angle2), 1]);
      m = new Matrix4().identity();
      rotateAxis(m, m, pi/2, [0, 1, 0]);
      m.transformVector(p3, p3);
      p3 = [p3[0], p3[1], p3[2]];

      [p1, p2, p3, p4] = [p2, p1, p4, p3];
    break;

    default:
    break;
  }

  return {
    p1, p2, p3, p4
  }
}



export function getSphericalPgonMeshData({orbifold, nSamplesPerSide, nLevels}) {
  const expandedVertices = [];
  const layoutType = getLayoutType(orbifold);
  const {p1, p2, p3, p4} = getSphericalParams(
    layoutType,
    getLayoutParams(layoutType, orbifold)
  );
  let centroid;
  let sides;
  if (p4 !== undefined) {
    centroid = vec3.vec3_add(p1, vec3.vec3_add(p2, vec3.vec3_add(p3, p4)));
    centroid = vec3.vec3_scale(centroid, 1/4);
    sides = [[p1, p2], [p2, p4], [p4, p3], [p3, p1]];
  } else {
    centroid = vec3.vec3_add(p1, vec3.vec3_add(p2, p3));
    centroid = vec3.vec3_scale(centroid, 1/3);
    sides = [[p1, p2], [p2, p3], [p3, p1]];
  }

  sides.forEach(([from, to], i) => {
    const intermediatePoints = [];
    const direction = vec3.vec3_unit(vec3.vec3_add(to, vec3.vec3_scale(from, -1)));
    const dist = vec3.vec3_dist(from, to);
    const delta = dist / (nSamplesPerSide - 1);
    for (let i = 0; i < nSamplesPerSide; i++) {
      let p = vec3.vec3_add(from, vec3.vec3_scale(direction, delta * i));
      intermediatePoints.push(p);
    }
    //expandedVertices.push(from);
    expandedVertices.push.apply(expandedVertices, intermediatePoints);
    //expandedVertices.push(to);
  });

  let levels = [];
  for (let l = 1; l < nLevels; l++) {
    let mappedVertices = expandedVertices.map(p => {
      const scale = (nLevels - l) / nLevels;
      let pScaled = vec3.vec3_add(
        vec3.vec3_scale(centroid,  1- scale),
        vec3.vec3_scale(p, scale)
      );
      return pScaled;
    });
    levels.push(mappedVertices);
  }
  levels.unshift(expandedVertices);
  levels.push([centroid]);
  levels.reverse();
  return levels;
};

export function sphericalTriangle(A, B, C) {
  const S = (A + B + C) / 2;
  const a = 2 * asin(sqrt(-cos(S)*cos(S-A) / (sin(B)*sin(C))));
  const b = 2 * asin(sqrt(-cos(S)*cos(S-B) / (sin(C)*sin(A))));
  const c = 2 * asin(sqrt(-cos(S)*cos(S-C) / (sin(A)*sin(B))));
  return [a, b, c];
}


