const spherical2 = sphericalGroups.slice(1).map((h, n) => {
  return   {
    type: 'spherical',
    orbifold: h,
    motif: {
      color: {
        type: 'radial-gradient',
        name: 'movie1',
        number: undefined,
        url: 'img/earth1.jpg',
        movie: null,
        nSamplesPerSide: 10,
        nLevels: 5,
        concentric: false,
        pgonSize: 1024,
        steps: 10,
        onStep: (step) => {
          return 'rgb(200, 150, 150)';
        }
      },
      depth: {
        type: 'radial-gradient',
        name: undefined,
        number: undefined,
        url: 'img/black.png',
        movie: null,
        nSamplesPerSide: 10,
        nLevels: 5,
        pgonSize: 1024*4,
        steps: 10,
        onStep: (step) => {
          const c = Math.ceil(Math.pow((1 - step), 8) * 255/1.5);
          return 'rgb('+ [c, c, c] +')';
        }
      },
      mesh: {
        type: 'sphericalPoly',
        flat: 0,
        nSamplesPerSide: 50,
        nLevels: 20,
        onIter: (level, sample) => {
          //return true;
          const nSamplesPerSide = 50;
          const levelOffset = 10;
          const sampleSize = 20;
          const sampleStart = (level * levelOffset) % nSamplesPerSide;
          //const sampleStart = (Math.sin(level / 100 * Math.PI * 2) + 1) / 2 * nSamplesPerSide;
          const sampleEnd = ((sampleStart + sampleSize) % nSamplesPerSide);
          if (sampleEnd < sampleStart) {
            return sample < sampleEnd || sample > sampleStart;
          }
          return sample > sampleStart && sample < sampleEnd;
          if (level === 0) {
            return false;
          }
          return !!(level % 10 <= 1) || !!(sample % 20 <= 1);
          return true;
        }
      }
    }
  };
});

