    return {
      x: 0,
      y: 0,
      ratio: -1
    };
  let ratio = sqrt(squaredRatio);
  let out = {
    x: x,
    y: y,
    ratio: ratio > 1000? -1 : ratio,
    a: a,
    b: b
  };

