function getGenericPgonMeshData({centroid, sides, nSamplesPerSide, nLevels}) {
  const expandedVertices = [];
  sides.forEach(([from, to], i) => {
    const intermediatePoints = [];
    const direction = vec3.vec3_unit(vec3.vec3_add(to, vec3.vec3_scale(from, -1)));
    const dist = vec3.vec3_dist(from, to);
    const delta = dist / (nSamplesPerSide - 1);
    for (let i = 0; i < nSamplesPerSide; i++) {
      let p = vec3.vec3_add(from, vec3.vec3_scale(direction, delta * i));
      intermediatePoints.push(p);
    }
    expandedVertices.push.apply(expandedVertices, intermediatePoints);
  });

  let levels = [];
  for (let l = 1; l < nLevels; l++) {
    let mappedVertices = expandedVertices.map(p => {
      const scale = (nLevels - l) / nLevels;
      let pScaled = vec3.vec3_add(
        vec3.vec3_scale(centroid,  1- scale),
        vec3.vec3_scale(p, scale)
      );
      return pScaled;
    });
    levels.push(mappedVertices);
  }
  levels.unshift(expandedVertices);
  levels.push([centroid]);
  levels.reverse();
  return levels;
};

export function getTransforms(state) {
  const row1 = [];
  const row2 = [];
  const row3 = [];
  const indices = [];
  const callback = (transform) => {
    indices.push(indices.length);
    row1.push.apply(row1, [transform[0], transform[1], transform[2], transform[12]]);
    row2.push.apply(row2, [transform[4], transform[5], transform[6], transform[13]]);
    row3.push.apply(row3, [transform[8], transform[9], transform[10], transform[14]]);
  };
  if (state.spherical || state.type == 'spherical') {
    layoutSpherical(state.orbifold, callback);
  } else if (state.hyperbolic || state.type == 'hyperbolic') {
    layoutHyperbolic(state.orbifold, state.iterations, callback);
  } else if (state.euclidean || state.type == 'euclidean') {
    layoutEuclidean(state.orbifold, state.iterations, callback);
  }
  return [row1, row2, row3, indices];
}

export function getTransformInstance(gl, opt) {
  return new Transform(gl, opt);
}

export function polygon(gl, textures, config, state, vs, fs, getPgonMeshData, getUVs) {
  const meshData = getPgonMeshData(Object.assign({}, state, config));
  const centroid = meshData[0][0];
  const pos = [];
  const pos2 = [];
  const pos3 = [];
  const cols = [];
  const depth = [];
  const l = meshData.length;
  const {onIter = () => true} = config;
  let texCoords;
  //TODO(nico) figure out the right coords
  texCoords = getUVs(state);

  const distances = meshData[meshData.length -1].map((p, i) => {
    return vec3.vec3_dist(p, centroid);
  });

  for (let row = 0; row < meshData.length - 1; row++) {
    let rowCurrent = meshData[row];
    let rowNext = meshData[row + 1];
    let len = rowNext.length;
    // center point of mesh
    if (rowCurrent.length == 1) {
      rowNext.forEach((p, j) => {
        let next = (j + 1) % len;
        if (onIter(row, j)) {
          pos.push(...centroid, ...p, ...rowNext[next]);
          pos2.push(...p, ...rowNext[next], ...centroid);
          pos3.push(...rowNext[next], ...centroid, ...p);

          depth.push(0, distances[j], distances[next]);
          depth.push(distances[j], distances[next], 0);
          depth.push(distances[next], 0, distances[j]);

          cols.push(
            row, row, row,
            row+1, row+1, row+1,
            row+1, row+1, row+1
          );
        }
      });
    } else {
      rowCurrent.forEach((r, i) => {
        let next = (i + 1) % len;
        let nextNext = (next + 1) % len;
        let rNext = rowCurrent[next];
        let p = rowNext[i];
        let pNext = rowNext[next];

        if (onIter(row, i)) {
          pos.push(...r, ...p, ...pNext
          );
          pos2.push( ...p, ...pNext, ...r
          );
          pos3.push(...pNext, ...r, ...p);

          depth.push(distances[i], distances[i], distances[next]);
          depth.push(distances[i], distances[next], distances[i]);
          depth.push(distances[next], distances[i], distances[i]);

          cols.push(
            row, row, row,
            row+1, row+1, row+1,
            row+1, row+1, row+1
          );

          pos.push(...rNext, ...r, ...pNext);
          pos2.push(...r, ...pNext, ...rNext);
          pos3.push(...pNext, ...rNext, ...r);

          depth.push(distances[next], distances[i], distances[next]);
          depth.push(distances[i], distances[next], distances[next]);
          depth.push(distances[next], distances[next], distances[i]);

          cols.push(
            row, row, row,
            row, row, row,
            row+1, row+1, row+1
          );
        }
      });
    }
  }

  const [row1, row2, row3, indices] = getTransforms(state);
  const instanceCount = row1.length / 4;

  return new Promise((resolve, reject) => {
    const sourceBuffers = {
      positions: new Buffer(gl, {data: new Float32Array(pos)}),
      positions2: new _Attribute(gl, {
        buffer: new Buffer(gl, {data: new Float32Array(pos2)}),
        size: 3
      }),
      positions3: new _Attribute(gl, {
        buffer: new Buffer(gl, {data: new Float32Array(pos3)}),
        size: 3
      }),
      depth: new _Attribute(gl, {
        buffer: new Buffer(gl, {data: new Float32Array(depth)}),
        size: 3
      }),
      cols: new _Attribute(gl, {
        buffer: new Buffer(gl, { data: new Float32Array(cols) }),
        size: 3
      }),
      row1: new _Attribute(gl, {
        buffer: new Buffer(gl, { data: new Float32Array(row1) }),
        instanced: 1,
        size: 4
      }),
      row2: new _Attribute(gl, {
        buffer: new Buffer(gl, { data: new Float32Array(row2) }),
        instanced: 1,
        size: 4
      }),
      row3: new _Attribute(gl, {
        buffer: new Buffer(gl, { data: new Float32Array(row3) }),
        instanced: 1,
        size: 4
      }),
      instanceNumber: new _Attribute(gl, {
        buffer: new Buffer(gl, { data: new Float32Array(indices) }),
        instanced: 1,
        size: 1
      })
    };

    const feedbackBuffers = {
      'vPosition': new Buffer(gl, {data: new Float32Array(pos.length * instanceCount)}),
      'vNormal': new Buffer(gl, {data: new Float32Array(pos.length * instanceCount)}),
      'vColor': new Buffer(gl, {data: new Float32Array(pos.length * instanceCount)})
    };

    const model = new Model(gl, {
      vs,
      fs,
      isInstanced: 1,
      instanceCount,
      vertexCount: pos.length / 3,
      uniforms: {
        sDepth: textures[0],
        sColor: textures[1],
        center: centroid,
        texCoords
      },
      drawMode: gl.TRIANGLES,
      attributes: sourceBuffers
    });

    resolve({
      model,
      transform: getTransformInstance(gl, {
        vs,
        instanceCount,
        isInstanced: 1,
        elementCount: pos.length / 3,
        sourceBuffers,
        feedbackBuffers,
        varyings: ['vPosition', 'vNormal', 'vColor']
      })
    });
  });
}

const solids = {
  getQuadEdge(vertices, faces) {
    const v = [];
    for (let i = 0; i < vertices.length; i+=3) {
      v.push([
        vertices[i],
        vertices[i + 1],
        vertices[i + 2]
      ]);
    }
    const f = faces.map(f => f.vertIdxs.map(f => f + 1));
    const g = JSONReadCell({
      name: 'poly',
      v,
      f
    });
    //clean(g);
    return g;
  },

  getStellationFaces(vertices, faces) {
    const quadEdge = this.getQuadEdge(vertices, faces);
    const ans = [];
    quadEdge.eachFace((f, i) => {
      const n = vec3.vec3_unit(f.getNormal());
      const c = f.getCentroid();
      const e = f.getEdge();
      const otherFace = e.Right();
      const otherNormal = vec3.vec3_unit(otherFace.getNormal());
      const v = vec3.vec3_scale(vec3.vec3_add(e.Org().pos, e.Dest().pos), 0.5);

      const alpha = acos(vec3.vec3_dot(n, otherNormal));

      // TODO(nico): need a param for general elevation
      // this would only work with solids that can be stellated
      const height = tan(alpha) * vec3.vec3_dist(c, v);
      const elevationVertex = vec3.vec3_add(c, vec3.vec3_scale(n, height));
      faces[f.id].elevationVertex = elevationVertex;

      //const faceVertices = [];
      //let centroid = [0, 0, 0];
      //let j = 0;
      //f.eachEdge(e => {
        //const p = e.Org().pos;
        //faceVertices.push(p);
        //centroid = vec3.vec3_add(p, centroid);
        //j++;
      //});
      //const nFace = j;
      //centroid = vec3.vec3_scale(centroid, 1/nFace);
      //const sides = faceVertices.map((v, i) => {
        //return [v, faceVertices[(i + 1) % faceVertices.length]];
      //});
      ////debugger;
      //ans.push({
        //elevation: true,
        //vertices: faceVertices,
        //length: nFace,
        //centroid,
        //sides,
        //iters: [1, 1, 1]
      //});

      f.eachEdge(e => {
        const o = e.Org().pos;
        let d = e.Dest().pos;
        //d = vec3.vec3_mid(o, d);
        const centroid = vec3.vec3_scale(vec3.vec3_add(o, vec3.vec3_add(d, elevationVertex)), 1/3);
        const faceVertices = [elevationVertex, o, d];
        //get pgonMeshData from faces
        const sides = faceVertices.map((v, i) => {
          return [v, faceVertices[(i + 1) % faceVertices.length]];
        });
        ans.push({
          elevation: true,
          elevationVertex,
          vertices: faceVertices,
          length: 3,
          centroid,
          sides,
          iters: [1, 1, 1]
        });
      });
    });
    return ans;
  },

  getFaces(vertices, faceTypes, faceVerts) {
    // create faces
    return faceTypes.filter((a, i) => !(i % 2)).map((n, i) => {
      const indices = [];
      while (n--) {
        indices.push(faceVerts.shift());
      }
      let centroid = [0, 0, 0];
      let refVertex = false;
      // compute vertices
      const faceVertices = indices.map((i) => {
        const v = [
          vertices[i * 3],
          vertices[i * 3 + 1],
          vertices[i * 3 + 2]
        ];
        centroid = vec3.vec3_add(v, centroid);
        return v;
      });
      const nFace = indices.length;
      centroid = vec3.vec3_scale(centroid, 1/nFace);

      //get pgonMeshData from faces
      const sides = faceVertices.map((v, i) => {
        return [v, faceVertices[(i + 1) % faceVertices.length]];
      });

      let angle = 0;
      const faceCoords = faceVertices.map((v) => {
        const coord = vec3.vec3_unit(vec3.vec3_sub(v, centroid));
        if (!refVertex) {
          angle = 0;
        } else {
          angle += acos(vec3.vec3_dot(refVertex, coord));
        }
        refVertex = coord;
        return [0.5 + 0.5 * cos(angle), 0.5 + 0.5 * sin(angle)];
      });

      return {
        vertIdxs: indices,
        vertices: faceVertices,
        texCoords: faceCoords,
        length: nFace,
        centroid,
        sides,
        iters: [1, 1, 1]
      };
    });
  },

  getTriangulatedFaces(faces) {
    const triangulatedFaces = [];
    faces.forEach(f => {
      const {centroid, vertIdxs, vertices, texCoords, length} = f;
      if (length == 3) {
        triangulatedFaces.push(f);
        return;
      }
      vertices.forEach((v, i) => {
        const iNext = (i + 1) % length;
        const vNext = vertices[iNext];
        let fCentroid = vec3.vec3_add(centroid, vec3.vec3_add(v, vNext));
        fCentroid = vec3.vec3_scale(fCentroid, 1/3);
        triangulatedFaces.push({
          iters: [1, 1, 1],
          centroid: fCentroid,
          vertices: [centroid, v, vNext],
          texCoords: [[0.5, 0.5], texCoords[i], texCoords[iNext]],
          length: 3
        });
      });
    });
    return triangulatedFaces;
  },

  getModel(vertices, faceTypes, faceVerts, stellated) {
    // create faces
    const faces = this.getFaces(vertices, faceTypes, faceVerts);
    // create stellation faces
    if (stellated) {
      const stellatedFaces = this.getStellationFaces(vertices, faces);
      faces.push.apply(faces, stellatedFaces);
    }
    // triangulate faces
    const triangulatedFaces = this.getTriangulatedFaces(faces);

    return {triangulatedFaces, faces}
  },

  // https://gratrix.net/polyhedra/webgl/poly.html
  getSolidFor(name, {stellated, optimalMap}, state) {
    return new Promise((resolve, reject) => {
      const data = solids[name];
      if (!data) {
        console.info('${name} not found');
        return;
      }
      if (data.triangulatedFaces) {
        resolve(data);
        return;
      }

      const [info, wythoff, config, vertCoords, vertIdxs, vertVerts, vertFaces, faceTypes, faceVerts] = data;
      const vertices = vertIdxs.map((idx) => vertCoords[idx]);

      // for myriad
      const vertices2 = [];
      let v = [];
      vertices.forEach((vi, i) => {
        if (i % 3 === 0) {
          if (v.length) {
            vertices2.push(v);
          }
          v = [];
        }
        v.push(vi);
      });
      if (v.length) {
        vertices2.push(v);
      }
      // end

      if (optimalMap) {
        let om = new OptimalMap();
        om.onmessage = (e) => {
          const model = this.getModel(e.data, faceTypes, faceVerts, stellated);
          Object.assign(data, model);
          resolve(model);
        };
        om.postMessage([vertices, getTransforms(state)]);
        return;
      }

      let model = this.getModel(vertices, faceTypes, faceVerts, stellated);
      // for myriad
      //let f = model.faces.map(fi => {
        //return fi.vertIdxs.map(i => i + 1);
      //});
      //let json = {
        //name: '',
        //v: vertices2,
        //f: f
      //};
      //debugger;
      // end

      Object.assign(data, model);
      resolve(data);
    });
  }
};

polyhedraNames.forEach(([name, name2], i) => solids[name] = polyhedraRaw[i]);

export function sectionSolid(gl, textures, config, state, vs, fs, getParams) {
  return new Promise((resolve, reject) => {

    let centroid;
    if (getParams) {
      centroid = getParams(state.orbifold[0], state.orbifold[1]).centroid;
    } else {
      centroid = [0.01, 0.01, 0.01];
    }
    solids.getSolidFor(config.solid, config, state)
      .then(({faces, triangulatedFaces}) => {
        const solid = faces;

        const allPos = [];
        const allPos2 = [];
        const allPos3 = [];
        const allCols = [];
        const allDepth = [];
        const allTexCoords = [];
        const allTexCoords2 = [];
        const allTexCoords3 = [];

        faces.forEach(f => {
          let {centroid, sides} = f;
          if (f.elevation) {
            centroid = f.elevationVertex;
          }
          const meshData = getGenericPgonMeshData(Object.assign({centroid, sides}, state, config));
          const pos = [];
          const pos2 = [];
          const pos3 = [];
          const cols = [];
          const depth = [];
          const l = meshData.length;
          //const centroid = meshData[0][0];
          const {onIter = () => true, circumbscribed = false, nLevels} = config;

          const distances = meshData.map(md => {
            return Math.max.apply(Math, md.map((p, i) => {
              return vec3.vec3_dist(p, centroid);
            }));
          });

          const longestDistance = Math.max.apply(Math, distances);

          for (let row = 0; row < meshData.length - 1; row++) {
            let rowCurrent = meshData[row];
            let rowNext = meshData[row + 1];
            let len = rowNext.length;
            // center point of mesh
            if (rowCurrent.length == 1) {
              rowNext.forEach((p, j) => {
                let next = (j + 1) % len;
                let rowNextNext = rowNext[next];
                const iterRes = +onIter(row, j, f, rowNext.length);
                if (iterRes) {
                  if (iterRes !== 1) {
                    p = vec3.vec3_add(vec3.vec3_scale(centroid, 1 - iterRes), vec3.vec3_scale(p, iterRes));
                    rowNextNext = vec3.vec3_add(vec3.vec3_scale(centroid, 1 - iterRes), vec3.vec3_scale(rowNextNext, iterRes));
                  }
                  pos.push(...centroid, ...p, ...rowNextNext);
                  pos2.push(...p, ...rowNextNext, ...centroid);
                  pos3.push(...rowNextNext, ...centroid, ...p);

                  depth.push(...centroid, ...centroid, ...centroid);

                  cols.push(
                    row, row, row,
                    row+1, row+1, row+1,
                    row+1, row+1, row+1
                  );
                }
              });
            } else {
              rowCurrent.forEach((r, i) => {
                let next = (i + 1) % len;
                let nextNext = (next + 1) % len;
                let rNext = rowCurrent[next];
                let p = rowNext[i];
                let pNext = rowNext[next];

                const iterRes = +onIter(row, i, f, rowCurrent.length);
                if (iterRes) {
                  if (iterRes !== 1) {
                    p = vec3.vec3_add(vec3.vec3_scale(centroid, 1 - iterRes), vec3.vec3_scale(p, iterRes));
                    r = vec3.vec3_add(vec3.vec3_scale(centroid, 1 - iterRes), vec3.vec3_scale(r, iterRes));
                    rNext = vec3.vec3_add(vec3.vec3_scale(centroid, 1 - iterRes), vec3.vec3_scale(rNext, iterRes));
                    pNext = vec3.vec3_add(vec3.vec3_scale(centroid, 1 - iterRes), vec3.vec3_scale(pNext, iterRes));
                  }

                  pos.push(...r, ...p, ...pNext);
                  pos2.push( ...p, ...pNext, ...r);
                  pos3.push(...pNext, ...r, ...p);

                  depth.push(...centroid, ...centroid, ...centroid);

                  cols.push(
                    row, row, row,
                    row+1, row+1, row+1,
                    row+1, row+1, row+1
                  );

                  pos.push(...rNext, ...r, ...pNext);
                  pos2.push(...r, ...pNext, ...rNext);
                  pos3.push(...pNext, ...rNext, ...r);

                  depth.push(...centroid, ...centroid, ...centroid);

                  cols.push(
                    row, row, row,
                    row, row, row,
                    row+1, row+1, row+1
                  );
                }
              });
            }
          }
          allPos.push.apply(allPos, pos);
          allPos2.push.apply(allPos2, pos2);
          allPos3.push.apply(allPos3, pos3);
          allCols.push.apply(allCols, cols);
          allDepth.push.apply(allDepth, depth);
        });

        const [row1, row2, row3, indices] = getTransforms(state);
        const instanceCount = row1.length / 4;

        const sourceBuffers = {
          positions: new Buffer(gl, {data: new Float32Array(allPos)}),
          positions2: new _Attribute(gl, {
            buffer: new Buffer(gl, {data: new Float32Array(allPos2)}),
            size: 3
          }),
          positions3: new _Attribute(gl, {
            buffer: new Buffer(gl, {data: new Float32Array(allPos3)}),
            size: 3
          }),
          depth: new _Attribute(gl, {
            buffer: new Buffer(gl, {data: new Float32Array(allDepth)}),
            size: 3
          }),
          cols: new _Attribute(gl, {
            buffer: new Buffer(gl, { data: new Float32Array(allCols) }),
            size: 3
          }),
          row1: new _Attribute(gl, {
            buffer: new Buffer(gl, { data: new Float32Array(row1) }),
            instanced: 1,
            size: 4
          }),
          row2: new _Attribute(gl, {
            buffer: new Buffer(gl, { data: new Float32Array(row2) }),
            instanced: 1,
            size: 4
          }),
          row3: new _Attribute(gl, {
            buffer: new Buffer(gl, { data: new Float32Array(row3) }),
            instanced: 1,
            size: 4
          }),
          instanceNumber: new _Attribute(gl, {
            buffer: new Buffer(gl, { data: new Float32Array(indices) }),
            instanced: 1,
            size: 1
          })
        };

        const feedbackBuffers = {
          'vPosition': new Buffer(gl, {data: new Float32Array(allPos.length * instanceCount)}),
          'vNormal': new Buffer(gl, {data: new Float32Array(allPos.length * instanceCount)}),
          'vColor': new Buffer(gl, {data: new Float32Array(allPos.length * instanceCount)})
        };

        const model = new Model(gl, {
          vs: vs,
          fs: fs,
          isInstanced: 1,
          instanceCount,
          vertexCount: allPos.length / 3,
          uniforms: {
            sDepth: textures[0],
            sColor: textures[1],
            center: centroid,//[20, 20, 20],
            //texCoords
          },
          drawMode: gl.TRIANGLES,
          attributes: sourceBuffers
        });

        resolve({
          model,
          transform: getTransformInstance(gl, {
            vs: vs,
            instanceCount,
            isInstanced: 1,
            elementCount: allPos.length / 3,
            sourceBuffers,
            feedbackBuffers,
            varyings: ['vPosition', 'vNormal', 'vColor']
          })
        });
      });
    });
}



