function build_quadedge(verts, faces) {
  let cell = Cell.make();
  let vertex1;

  cell.eachVertex(v => {
    vertex1 = v;
  });

  let v = verts[0];
  v.vertex = vertex1;
  v.vertex.pos = v.p.slice();
  v.vertex.setId(v.no);

  makeFace(cell, v.arclist[0][0].f);

  while (true) {
    let instantiated = 1;
    verts.forEach(v => {
      if (v.vertex != 0 && !v.instantiated) {
        makeVertex(cell, v);
      }
      instantiated = v.instantiated && instantiated;
    });
    if (instantiated) {
      break;
    }
  }

  cell.eachFace(f => f.data = 0);

  return cell;
}

export function JSONReadCell(json) {
  let verts = json.v.map((v, i) => {
    return {
      p: v,
      no: i,
      done: 0,
      vertex: 0,
      instantiated: 0,
      arclist: []
    };
  });
  let faces = json.f.map((face, i) => {
    let f = new Tface();
    f.face = 0;
    f.no = i;
    face.forEach(index => f.vlist.push(verts[index -1]));
    add_arcs(f.vlist, f);
    return f;
  });

  return build_quadedge(verts, faces);
}

// TODO(nico): build a function that retruns float arrays and normals.
export function JSONWriteCell(cell, pos='pos', flatten=false) {
  let ans = { v: [], f: [], n: [] };

  let i = 1;
  cell.eachVertex(v => {
    v.setId(i++);
    if (flatten) {
      ans.v.push.apply(ans.v, v[pos]);
    } else {
      ans.v.push(v.pos);
    }
  });
  cell.eachFace(f => {
    let indices = [];
    f.eachEdge(e => indices.push(e.Org().id + (flatten ? -1 : 0)));
    if (flatten) {
      ans.f.push.apply(ans.f, indices);
    } else {
      ans.f.push(indices);
    }
  });
  return ans;
}

function precisionRound(number, precision) {
  var factor = Math.pow(10, precision);
  return Math.round(number * factor) / factor;
}

